function sliderMain() {
    var swiper = new Swiper('.main .swiper', {
        slidesPerView: '1',
        effect: 'fade',
        fadeEffect: {
            crossFade: true
        },
        navigation: {
            nextEl: '.main .swiper-button-next',
            prevEl: '.main .swiper-button-prev',
        },
        pagination: {
            el: ".main .swiper-pagination",
            clickable: true,
        },
    })
}

function sliderNews() {
    var swiper = new Swiper('.news .swiper', {
        slidesPerView: '3',
        spaceBetween: 40,
        loop: true,
        navigation: {
            nextEl: '.news .swiper-button-next',
            prevEl: '.news .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            465: {
                slidesPerView: 1.5,
            },
            640: {
                slidesPerView: 2,
                spaceBetween: 15,
            },
            1300: {
                slidesPerView: 3,
                spaceBetween: 40,
            },
        }
    })
}

